<template>
    <div id="swe-dashboard">
        <h3>
            Time In and Time out of SWE
        </h3>
        <div class="time-in-out">
            <button  v-if="!timedIn" @click="timeIn" class="btn black">Time-in</button>
             <button  v-else-if="timedIn" @click="timeOut" class="btn black">Time-out</button>           
        </div>
        <div v-if="!timedIn"> </div>
        <div v-else>
            You have timed in : {{timeinDisplay}}
        </div>
        <div v-if="!timedOut"> </div>
        <div v-else>
            You have timed out : {{timeout}}
        </div>

    </div>
</template>

<script>
import firebase from 'firebase'
import db from '../firebase'
import {mapGetters, mapActions} from 'vuex';


export default {
    name: 'swe-dashbaord',
    data(){
        return {
            timedIn: false,
            timeinDisplay: '',
            timedOut: false,
            timeout: '',
            timeref: '',
        }
    },
    methods: {
        timeIn(e){
            this.timeinDisplay = new Date().toLocaleString()

            db.collection('Timsheet').add({
                Name: firebase.auth().currentUser.email,
                Timein: new Date(),
                Timeout: '',
                NightDiff: '',
                TotalHours: '',
                Overtime: '',
                Remarks: '',
            }).then(docRef => {
                this.timedIn = true
                this.timein = new Date()
                this.timeref = docRef.id
                console.log(this.timeref)
                alert('you have successfully timed in')
            })
            e.preventDefault()
        },
        timeOut(e){
            let date1 = this.timein
            let date2 = new Date()
            let hours = Math.abs((date2.getTime() - date1.getTime()) / 36e5)
            let remarks = 'Undertime'
            let time = 0;
            //edit for minutes... 15 mins
            switch (date1.getHours()){
                case 24:
                    time = 6
                    break;
                case 21 || 23:
                    time = 7
                    break;
                case 22:
                    time = 8
                    break;
            }
            console.log(time)
            
            db.collection('Timsheet').doc(this.timeref).update({
                Timeout: new Date(),
                TotalHours: hours.toFixed(2),
                NightDiff: time,
            }).then(()=>{
                this.timedOut = true
                this.timeout = new Date().toLocaleString()
                alert('you have successfully timed out')
                if(hours < 9) {
                    db.collection('Timsheet').doc(this.timeref).update({
                        Remarks: remarks.toUpperCase()
                        })
                    }
                else if(hours > 9){
                    let remarks = 'overtime'
                    db.collection('Timsheet').doc(this.timeref).update({
                        Remarks: remarks.toUpperCase()
                        })
                    }

                firebase.auth().signOut().then(()=>{
                this.$router.push('/login')
                    })
                }).catch(function(error){
                alert("Error in updating", error)
            })
            e.preventDefault()
        }
    }
}
</script>

<style>

</style>



